import { PayloadAction } from '@reduxjs/toolkit';
import { startListening } from 'app/store/actionListenerMiddleware';
import { changeThemeAction } from 'modules/ThemeNavigation/actions';

startListening({
  actionCreator: changeThemeAction,
  effect: (action) => {
    handleSetThemeClassname(action);
  },
});

function handleSetThemeClassname(action: PayloadAction<{ themeName: string }>) {
  window.ReactNativeWebView?.postMessage(JSON.stringify(action));
}
